@import url(https://fonts.googleapis.com/css?family=Open+Sans:300,400,700);
*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: inherit; }

html {
  box-sizing: border-box;
  font-size: 62.5%; }

body {
  font-family: 'Open Sans', sans-serif;
  line-height: 1.6; }

.App {
  width: 100%;
  height: 100%; }

.weather {
  width: 100%;
  height: 100%; }

.weather-container {
  margin: 0 auto;
  padding-top: 3rem;
  max-width: 100%;
  width: 50%;
  max-height: 100%;
  height: auto;
  text-align: center; }

.input-form input {
  height: 3.5rem;
  width: 70%;
  font-size: 3rem;
  padding: 1rem; }

.next-day a {
  background-color: red;
  width: 3rem;
  height: 3rem; }

.today-weather {
  margin-top: 3rem;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
  font-size: 2rem; }

.today-weather-left img {
  width: 8rem;
  height: auto; }

